import angular      from 'angular';
import uiRouter     from 'angular-ui-router';
import $resource    from 'angular-resource';
import ngDialog     from 'ng-dialog';
import AppComponent from './app.component';


import 'ng-dialog/css/ngDialog.min.css';
import 'ng-dialog/css/ngDialog-theme-default.min.css';
import 'ng-dialog/js/ngDialog.min.js';
import 'normalize.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'font-awesome/css/font-awesome.min.css';  
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/js/bootstrap.js';

import NavigationComponent from './components/navigation/navigation';
import HomeComponent from './pages/home/home';
import CreateComponent from './pages/create/create';
import LoginComponent from './pages/login/login';
import RegisterComponent from './pages/register/register';
import BlogComponent from './pages/blog/blog';

import AuthService from './services/AuthService';
import BlogService from './services/BlogService';

import $localStorage from './services/LocalStorageService';

import GoatsService from './services/GoatsService';


// import our default styles for the whole application

angular
    .module('app', [
        uiRouter,
        $resource,
        ngDialog,
        NavigationComponent.name,
        CreateComponent.name,
        HomeComponent.name,
        LoginComponent.name,
        RegisterComponent.name,
        BlogComponent.name
        
       
      
    ])
    .config(($locationProvider, $stateProvider, $urlRouterProvider) => {
        "ngInject";

        // Define our app routing, we will keep our layout inside the app component
        // The layout route will be abstract and it will hold all of our app views
        $stateProvider
            .state('app', {
                url: '/',
                abstract: true,
                template: '<app></app>'
            })

            // Dashboard page to contain our goats list page
            .state('app.home', {
                url: 'home',
                template: '<home></home>'
            })

            // Create route for our goat listings creator
            .state('app.blog', {
                url: 'blog',
                template: '<blog></blog>'
            })
            .state('app.login', {
                url: 'login',
                template: '<login></login>'
            })

            // Create route for our goat listings creator
            .state('app.register', {
                url: 'register',
                template: '<register></register>'
            });

        $urlRouterProvider.otherwise('/home');
         $locationProvider.html5Mode(true).hashPrefix('!');
    })
    .component('app', AppComponent)
    .factory('GoatsService', GoatsService)
    .factory('$localStorage', $localStorage)
    .factory('AuthService', AuthService)
    .factory('BlogService', BlogService)

const baseURL = 'https://tanyalyakhor.herokuapp.com/';
export {baseURL};



