class PostsRowController {
    constructor(BlogService) {
    	'ngInject';
        this.heading = 'Психологический Блог';
        this.posts = [];

        BlogService.query(response => {
            this.posts = response;
        }, response => {
            this.message = 'Извините, сервер временно недоступен'
        });
    }
}

export default PostsRowController;


